import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import { Link } from "react-scroll";

const styles = {
  root: {
    flexGrow: 1
  },
  grow: {
    marginRight: 20
  },

  navButton: {
    marginLeft: 30,
    fontWeight: 300,
    transition: "1s",
    "&:hover": {
      color: "#74AA9D",
      paddingRight: 20,
      paddingLeft: 20
    }
  },
  navLinks: {
    flexDirection: "row"
  },

  active: {
    color: "red"
  },

  menuButton: {
    marginLeft: -12,
    marginRight: 20,
    color: "#75A99D"
  },

  transparentAppBar: {
    backgroundColor: "rgba(50,50,51,0.8)"
  },

  primaryButton: {
    fontSize: 18,
    fontWeight: 400
  },
  show: {
    transform: "translate(0, 0)",
    transition: "transform .5s"
  },
  hide: {
    transform: "translate(0, -70px)",
    transition: "transform .5s"
  }
};

class ButtonHeader extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      shouldShow: null
    };

    this.lastScroll = null;

    this.handleScroll = this.handleScroll.bind(this);
  }
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll, { passive: true });
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll() {
    const lastScroll = window.scrollY;

    if (lastScroll === this.state.lastScroll) {
      return;
    }

    const shouldShow =
      this.lastScroll !== null ? lastScroll < this.lastScroll : null;

    if (shouldShow !== this.state.shouldShow) {
      this.setState(prevState => ({
        ...prevState,
        shouldShow
      }));
    }

    this.lastScroll = lastScroll;
  }

  getScrollClassName() {
    if (this.state.shouldShow === null) {
      return "";
    }

    return this.state.shouldShow
      ? this.props.classes.show
      : this.props.classes.hide;
  }

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <AppBar
          position="fixed"
          className={`${
            classes.transparentAppBar
          } ${this.getScrollClassName()}`}
        >
          <Toolbar>
            <Link
              activeClass="active"
              to="section1"
              spy={true}
              smooth={true}
              offset={0}
              duration={500}
              clickable
            >
              <Button color="inherit" className={classes.primaryButton}>
                Gavin Craig
              </Button>
            </Link>
            <div className={classes.navLinks}>
              <Link
                activeClass="active"
                to="section2"
                spy={true}
                smooth={true}
                offset={0}
                duration={500}
              >
                <Button color="inherit" className={classes.navButton}>
                  About
                </Button>
              </Link>
              <Link
                activeClass="active"
                to="section3"
                spy={true}
                smooth={true}
                offset={50}
                duration={500}
              >
                <Button color="inherit" className={classes.navButton}>
                  Techology
                </Button>
              </Link>

              <Link
                activeClass="active"
                to="section4"
                spy={true}
                smooth={true}
                offset={100}
                duration={500}
              >
                <Button color="inherit" className={classes.navButton}>
                  Projects
                </Button>
              </Link>
              <Link
                activeClass="active"
                to="section5"
                spy={true}
                smooth={true}
                offset={0}
                duration={500}
              >
                <Button color="inherit" className={classes.navButton}>
                  Contact
                </Button>
              </Link>
            </div>
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}

ButtonHeader.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(ButtonHeader);
