import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Image from "../images/device-electronics-keyboard-872958.jpg";
import Avatar from "@material-ui/core/Avatar";
import Chip from "@material-ui/core/Chip";
import {
  DiJavascript1,
  DiNodejsSmall,
  DiReact,
  DiHtml5,
  DiCss3
} from "react-icons/di";
import Slide from "react-reveal/Slide";
import Pulse from "react-reveal/Pulse";

const styles = theme => ({
  root: {
    flexGrow: 1,
    justify: "center",
    alignItems: "center",
    backgroundImage: `url(${Image})`,
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center",
    padding: "5%",
    paddingLeft: 0,
    minHeight: "100vh"
  },

  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: "center",
    color: theme.palette.text.secondary
  },
  paperBg: {
    width: "100%",
    height: "100%",
    backgroundImage: `url(${Image})`,
    backgroundSize: "cover",
    backgroundPosition: "absolute",
    top: 0,
    left: 0
  },

  cardParagraphs: {
    justifyContent: "center",
    alignItems: "start",
    textAlign: "start",

    width: "100%",
    margin: 0
  },

  paragraphText: {
    fontWeight: 350,
    color: "#f2f2f2"
  },

  title: {
    color: "#fefefe"
  },
  softwareTab: {
    backgroundColor: "rgba(33, 33, 33,0.9)",
    padding: 10,
    margin: 0,
    minWidth: "10vw",
    maxWidth: "15vw",
    alignItems: "center",
    textAlign: "center",
    borderTopRightRadius: 8,
    marginTop: 110
  },

  [`@media (max-width: ${1200}px)`]: {
    softwareTab: {
      minWidth: "10vw",
      maxWidth: "40vw"
    },
    introContainer: {
      margin: 0,
      padding: 0
    },

    cardParagraphs: {
      padding: 0
    }
  },

  introContainer: {
    margin: 0,
    padding: "5%",
    backgroundColor: "rgba(33, 33, 33,0.9)"
  },

  chip: {
    margin: theme.spacing.unit
  }
});

function Software(props) {
  const { classes } = props;
  return (
    <div className={classes.root} id="section3">
      <div style={{ maxWidth: 1920 }}>
        <Slide left duration={700}>
          <div className={classes.softwareTab}>
            <Typography
              variant="h2"
              style={{
                margin: 0,
                fontSize: "2rem",
                fontWeight: 120,
                color: "#fefefe"
              }}
            >
              Techology
            </Typography>
          </div>
        </Slide>
        <Slide left delay={600} duration={1000}>
          <div className={classes.introContainer}>
            <div>
              <Grid container spacing={24} className={classes.cardParagraphs}>
                <Grid item md={4}>
                  <Typography
                    variant="h2"
                    gutterBottom
                    style={{ fontSize: 32, fontWeight: 120, color: "#fefefe" }}
                  >
                    Java
                  </Typography>

                  <Typography
                    component="p"
                    gutterBottom
                    className={classes.paragraphText}
                  >
                    4 years of programming experience with Java.
                  </Typography>
                  <Typography
                    component="p"
                    gutterBottom
                    style={{ marginTop: 10 }}
                    className={classes.paragraphText}
                  >
                    Java was the first language I learned and the primary
                    language taught by the university.
                  </Typography>

                  <Typography
                    component="p"
                    style={{ marginTop: 10 }}
                    className={classes.paragraphText}
                  >
                    I have a strong understanding of object oriented design and
                    efficient use of various data structures.
                  </Typography>
                </Grid>
                <Grid item md={4}>
                  <Typography
                    variant="h2"
                    gutterBottom
                    style={{ fontSize: 32, fontWeight: 120, color: "#fefefe" }}
                  >
                    Web Development
                  </Typography>
                  <Pulse delay={1000}>
                    <div>
                      <Chip
                        avatar={
                          <Avatar>
                            <DiHtml5 />
                          </Avatar>
                        }
                        label="HTML5"
                        clickable={false}
                        className={classes.chip}
                        color="Secondary"
                        avatarColorPrimary
                      />
                      <Chip
                        avatar={
                          <Avatar>
                            <DiCss3 />
                          </Avatar>
                        }
                        label="CSS"
                        clickable={false}
                        color="Secondary"
                        className={classes.chip}
                      />
                      <Chip
                        avatar={
                          <Avatar>
                            <DiReact />
                          </Avatar>
                        }
                        label="ReactJS"
                        clickable={false}
                        color="Secondary"
                        className={classes.chip}
                      />
                      <Chip
                        avatar={
                          <Avatar>
                            <DiJavascript1 />
                          </Avatar>
                        }
                        label="JavaScript"
                        clickable={false}
                        color="Secondary"
                        className={classes.chip}
                      />
                      <Chip
                        avatar={
                          <Avatar>
                            <DiNodejsSmall />
                          </Avatar>
                        }
                        label="NodeJS"
                        clickable={false}
                        color="Secondary"
                        className={classes.chip}
                      />
                    </div>
                  </Pulse>

                  <Typography
                    component="p"
                    style={{ marginTop: 10 }}
                    className={classes.paragraphText}
                  >
                    This site uses HTML, Css, ReactJs and the Google Material-UI
                    component library.
                  </Typography>
                  <Typography
                    component="p"
                    style={{ marginTop: 10 }}
                    className={classes.paragraphText}
                  >
                    I thoroughly enjoy learning about web technologies and
                    working on small web apps and sites.
                  </Typography>
                  <Typography
                    component="p"
                    style={{ marginTop: 10 }}
                    className={classes.paragraphText}
                  >
                    The majority of my experience with web technology comes from
                    personal research.
                  </Typography>
                </Grid>
                <Grid item md={4}>
                  <Typography
                    variant="h2"
                    gutterBottom
                    style={{ fontSize: 32, fontWeight: 120, color: "#fefefe" }}
                  >
                    Mobile Development
                  </Typography>

                  <Typography
                    component="p"
                    gutterBottom
                    className={classes.paragraphText}
                  >
                    Whilst working on my honours project I worked primarily with
                    React Native and NodeJS.
                  </Typography>
                  <Typography
                    component="p"
                    gutterBottom
                    style={{ marginTop: 10 }}
                    className={classes.paragraphText}
                  >
                    This has allowed me to gain a small amount of experience
                    with React Native and the approach to developing mobile
                    applications.
                  </Typography>
                </Grid>
              </Grid>
            </div>
          </div>
        </Slide>
      </div>
    </div>
  );
}

Software.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Software);
