import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import ButtonHeader from "../Header/ButtonHeader.js";
import Image from "../images/cup-desk-drink-434337.jpg";

const styles = theme => ({
  root: {
    flexGrow: 1,
    minWidth: 100,
    minHeight: "700",
    justify: "center",
    alignItems: "center",
    overflowX: "hidden",
    overflowY: "hidden"
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: "center",
    color: theme.palette.text.secondary
  },
  paperBg: {
    width: "100%",
    height: "100%",
    backgroundImage: `url(${Image})`,
    backgroundSize: "cover",
    backgroundPosition: "top",
    top: 0,
    left: 0
  },

  splashContainer: {
    minHeight: "100vh",

    justify: "center",
    alignItems: "center"
  },

  mainTitle: {
    textAlign: "center",
    color: "#75A99D",
    textShadow:
      "-1px -1px 0 #ffff, 1px -1px 0 #ffff, -1px 1px 0 #ffff, 1px 1px 0 #ffff"
  },

  subTitle: {
    textAlign: "center",
    color: "#555555"
  }
});

function NewHeader(props) {
  const { classes } = props;
  return (
    <div className={classes.root} id="section1">
      <Paper className={classes.paperBg}>
        <ButtonHeader />
        <Grid
          className={classes.splashContainer}
          container
          spacing={24}
          direction="column"
          justify="center"
          alignItems="center"
        >
          <Grid item xl={12}>
            <Typography variant="h1" className={classes.mainTitle}>
              Gavin Craig
            </Typography>
          </Grid>
          <Grid item xl={12} direction="column">
            <Typography variant="subheading" className={classes.subTitle}>
              BSc Hons Computer Science
            </Typography>
            <Typography variant="subheading" className={classes.subTitle}>
              Alumnus of the University of Strathclyde
            </Typography>
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
}

NewHeader.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(NewHeader);
