import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import { CardHeader } from "@material-ui/core";
import Image from "../images/honours-app-3-displays-01.png";
import app from "../images/iphone-app-mockups-01.png";

const styles = theme => ({
  root: {
    flexGrow: 1,
    justify: "center",
    alignItems: "center",
    padding: "5%",
    minHeight: "100vh",
    overflowX: "hidden",
    outline: "none!important"
  },

  nowrap: {
    whiteSpace: "nowrap",
    wordBreak: "keep-all"
  },

  introContainer: {
    padding: "3%",
    alignItems: "center",
    justifyContent: "center",
    marginTop: "10vh",
    marginLeft: "10%",
    marginRight: "10%",
    backgroundColor: "rgb(33, 33, 33)",
    border: "none",
    maxHeight: "90vh",
    overflowY: "auto",
    overflowX: "hidden",
    outline: "none!important"
  },

  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: "center",
    color: theme.palette.text.secondary
  },
  paperBg: {
    width: "100%",
    height: "100%",
    backgroundImage: `url(${Image})`,
    backgroundSize: "cover",
    backgroundPosition: "absolute",
    top: 0,
    left: 0
  },

  splashContainer: {
    minHeight: "90vh",
    paddingTop: "20%",
    justify: "center",
    alignItems: "center"
  },

  cardParagraphs: {
    justify: "center",
    justifyContent: "center",
    alignItems: "start",
    textAlign: "start",
    paddingLeft: 40,
    width: "100%",
    paddingTop: 10
  },

  paragraphText: {
    fontSize: 18,
    fontWeight: 200,
    color: "#fefefe",
    marginBottom: 20
  },

  title: {
    color: "#fefefe",
    fontWeight: 200
  },

  subHeader: {
    color: "rgba(209, 209, 209)",
    fontWeight: 100
  },

  contactDetail: {
    color: "rgba(209, 209, 209)",
    fontWeight: 100,
    textAlign: "start",

    padding: 10
  },

  button: {
    color: "rgba(209, 209, 209)",

    padding: 5,
    fontSize: 24,
    fontWeight: 300
  },

  gitButton: {
    color: "rgba(209, 209, 209)",
    fontSize: 45,

    padding: 0
  },

  container: {
    alignItems: "center",
    justifyContent: "center"
  },

  emailIcon: { margin: theme.spacing.unit },

  gitIcon: { fontSize: 43 },

  rightIcon: {
    marginLeft: theme.spacing.unit
  },

  media: {
    height: 100
  }
});

function HonoursModal(props) {
  const { classes } = props;
  return (
    <Paper
      className={classes.introContainer}
      style={{ alignItems: "center", justifyContent: "center" }}
    >
      <Grid container spacing={24}>
        <Grid item lg={12}>
          <CardHeader
            classes={{
              title: classes.title,
              subheader: classes.subHeader
            }}
            titleTypographyProps={{ variant: "h2", gutterBottom: "true" }}
            title="Daily Blood Glucose Logger"
            subheader="React Native | NodeJS | MySQL"
            subheaderTypographyProps={{
              variant: "h4",
              gutterBottom: "true"
            }}
          />
        </Grid>

        <Grid item md={7} style={{ textAlign: "start", padding: "3%" }}>
          <Typography
            component="p"
            gutterBottom
            className={classes.paragraphText}
          >
            A mobile application for people living with type 1 diabetes.
            Developed for my honours project.
          </Typography>
          <Typography
            component="p"
            className={classes.paragraphText}
            gutterBottom
          >
            The application intended to offer a unique and convenient tool for
            people living with type 1 diabetes to log and monitor their blood
            sugar levels throughout the day.
          </Typography>

          <Typography
            component="p"
            className={classes.paragraphText}
            gutterBottom
          >
            An aim of this was to create an app that would allow users to gain a
            broader understanding of their blood glucose levels over time
            through various data visualisation methods.
          </Typography>

          <Typography
            component="p"
            className={classes.paragraphText}
            gutterBottom
          >
            Development involved exploring how to make an application that was
            intuitive and convenient to use throughout the day. This led to many
            design considerations and a 'UX First' approach to development.
          </Typography>

          <Typography component="p" className={classes.paragraphText}>
            The app uses colour and shape as an indication of blood glucose
            trends in order to create a minimal interface that is easy to digest
            for users with varying levels of smartphone experience.
          </Typography>
        </Grid>

        <Grid item md={5} style={{ textAlign: "start" }}>
          <img src={app} alt="Flowers in Chania" width="auto" height="450" />
        </Grid>
      </Grid>
    </Paper>
  );
}

HonoursModal.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(HonoursModal);
