import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import Grid from "@material-ui/core/Grid";
import Image from "../images/buildings-clear-sky-exterior-374023.jpg";
import { CardHeader } from "@material-ui/core";
import Fade from "react-reveal/Fade";
import ProjectCard from "./ProjectCard.js";
import HonoursCard from "./HonoursCard.js";

const styles = theme => ({
  root: {
    flexGrow: 1,
    justify: "center",
    alignItems: "center",
    backgroundImage: `url(${Image})`,
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center",
    padding: "5%",
    minHeight: "100vh",
    overflowX: "hidden"
  },

  introContainer: {
    padding: 20,
    width: "100%",
    alignItems: "center",
    justifyContent: "center",
    marginTop: "10vh",
    backgroundColor: "rgba(255, 255, 255,0.95)"
  },

  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: "center",
    color: theme.palette.text.secondary
  },
  paperBg: {
    width: "100%",
    height: "100%",
    backgroundImage: `url(${Image})`,
    backgroundSize: "cover",
    backgroundPosition: "absolute",
    top: 0,
    left: 0
  },

  splashContainer: {
    minHeight: "90vh",
    paddingTop: "20%",
    justify: "center",
    alignItems: "center"
  },

  cardParagraphs: {
    justify: "center",
    justifyContent: "center",
    alignItems: "start",
    textAlign: "start",
    paddingLeft: 40,
    width: "100%",
    paddingTop: 10
  },

  paragraphText: {
    fontWeight: 350
  }
});

function Projects(props) {
  const { classes } = props;
  return (
    <div className={classes.root} id="section4">
      <div style={{ maxWidth: 1920, margin: "0 auto" }}>
        <Fade up>
          <Grid container spacing={40} justify="flex-end" alignItems="flex-end">
            <Grid item xs={12}>
              <Card className={classes.introContainer}>
                <CardHeader
                  titleTypographyProps={{ variant: "h2", gutterBottom: "true" }}
                  title="Projects"
                  subheaderTypographyProps={{ variant: "subheading" }}
                  subheader="I enjoy creating and exploring small projects and business ideas. I have began to undertake more projects in my spare time to consolodate my learning."
                />
              </Card>
            </Grid>
            <Grid item md={6} justify="flex-end" alignItems="flex-end">
              <ProjectCard />
            </Grid>
            <Grid item md={6} justify="flex-end" alignItems="flex-end">
              <HonoursCard />
            </Grid>
          </Grid>
        </Fade>
      </div>
    </div>
  );
}

Projects.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Projects);
