import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Image from "../images/swshr-tee-shirts.JPG";
import Modal from "@material-ui/core/Modal";
import SwshrModal from "./SwshrModal.js";

const styles = {
  card: {
    maxWidth: 805
  },
  media: {
    height: 330
  }
};

class ProjectCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false
    };
  }

  handleOpen = () => {
    this.setState({ open: true });
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  render() {
    const { classes } = this.props;
    return (
      <div>
        <Modal
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          open={this.state.open}
          onClose={this.handleClose}
        >
          <SwshrModal />
        </Modal>
        <Card className={classes.card}>
          <CardActionArea>
            <CardMedia
              className={classes.media}
              image={Image}
              title="Original Swshr Tee Shirts"
            />
            <CardContent>
              <Typography gutterBottom variant="h5" component="h2">
                SWSHR LTD.
              </Typography>
              <Typography gutterBottom variant="subheading" component="h2" />
              <Typography gutterBottom variant="subheading" component="h2">
                An independent urban clothing brand started by a friend and I
                towards the end of 2017.
              </Typography>
            </CardContent>
          </CardActionArea>
          <CardActions>
            <Button size="small" color="primary" onClick={this.handleOpen}>
              Learn More
            </Button>
          </CardActions>
        </Card>
      </div>
    );
  }
}

ProjectCard.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(ProjectCard);
